@media (min-width: 320px) and (max-width: 575.98px) {
    .container-card {
        width: 320px;
        display: block;
        background-color: #fff;
        height: 3290px !important;
    }
    .card {
        
    }
    .popular {
        p {
            margin: 0;
        }
    }
    .slick-slider {
        margin: 0;
    }
    .hide {
        display: none !important;
    }
    .slick-list {
        height: 3230px !important;
        .slick-track {
            transform: translate3d(0px, 0px, 0px) !important;
            .slick-slide {
                width: 300px !important;
            }
        }
    }
    .container-brands {
        padding: 0;
        .brands-title {
            height: 100% !important;
            .brands-item {
                display: block;
                margin-bottom: 20px;    
                div:nth-child(1) {
                    img {
                        margin-left: 25px;
                    }
                } 
                div:nth-child(3) {
                    img {
                        margin-left: 25px;
                    }
                }       
            }
        }
    }
    .description {
        display: flex;
        justify-content: space-between;
        line-height: 0.5;
        margin: 0 0 0 -20px;
        .desc-left {
            color: #a0a0a0;
            font-family: "Arial MT";
            font-size: 13px;
            font-weight: 400;
        }
    }
    .card-body {
        height: auto;
    }
    .quantity {
        display: flex;
        justify-content: space-between;
        border-top: 1px solid rgba(0,0,0,.125);
        padding: 20px 0 0 0;
        margin-right: 20px;
        p {
            color: #a0a0a0;
            font-family: "Arial MT";
            font-size: 13px;
            font-weight: 400;
            line-height: 18px;
            margin-bottom: auto;
            margin-top: auto;
        }
        .number {
            border: 1px solid #dcdedd;
            padding: 5px 5px;
            input {
                width: 30px;
                text-align: center;
                border: none;
                border-left: 1px solid #dcdedd;
                border-right: 1px solid #dcdedd;
            }
        }
    }
    .card-footer {
        border: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.125);
        padding-bottom: 25px;
        margin-right: 20px;
    }
    .card-telo {
        border-bottom: none;
    }
    .text-card:nth-child(1) {

        margin: 30px 0 0 -10px;
    }
}
@media (min-width: 576px) and (max-width: 768px) {
    .container-card {
        width: 768px;
        background-color: #fff;
        height: 1240px;
    }
    .card-popular {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .card {
        width: 45%;
        margin: 0;
        .quantity {
            display: flex;
            justify-content: space-between;
            border-top: 1px solid rgba(0,0,0,.125);
            padding: 20px 0 20px 0;
            margin-right: 20px;
            p {
                color: #a0a0a0;
                font-family: "Arial MT";
                font-size: 13px;
                font-weight: 400;
                line-height: 18px;
                margin-bottom: auto;
                margin-top: auto;
            }
            .number {
                border: 1px solid #dcdedd;
                padding: 5px 5px;
                input {
                    width: 30px;
                    text-align: center;
                    border: none;
                    border-left: 1px solid #dcdedd;
                    border-right: 1px solid #dcdedd;
                }
            }
        }
    }
    .card-footer {
        height: 90px;
        border-bottom: 1px solid rgba(0,0,0,.125);
        border-top: none;
        .text-muted {
            padding-left: 15px;
        }
    }
    .card-group {
        display: none;
    }
    .card-hide {
        border-bottom: none !important;
    }
}