.container-footer {
    max-width: 1920px;
    margin: 0 auto;
    margin-top: 20px;
    background-color: #ffffff;
}
.footer-title {
    max-width: 1526px;
    margin: 0 auto;
    display: flex;
    padding-top: 25px;
}
.foot-item {
    column-count: 2;
    list-style-position: inside;
    padding-left: 0;
    ul {
        li {
            list-style: none;
            a {
                text-decoration: none;
                color: #222222;
                font-family: "BebasNeueBook";
                font-size: 18px;
                font-weight: 400;
                line-height: 24px;
                letter-spacing: 1.35px;
            }
        }
    }
}
.footer-item {
    display: flex;
    justify-content: space-around;
    .foot-title1 {
        column-count: 1;
        ul {
            li {
                list-style: none;
                a {
                    text-decoration: none;
                    color: #222222;
                    font-family: "Arial MT";
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 24px;
                }
            }
        }
    }
    .foot-title2 {
        a {
            color: #000000;
            font-family: "Arial MT";
            font-size: 18px;
            font-weight: 400;
            line-height: 32.23px;
            text-decoration: none;
        }
        p {
            &:nth-child(1):hover {
                opacity: 0.4 !important;
            }
        }
        .button {
            color: #222222;
            font-family: "Arial MT";
            font-size: 14px;
            font-weight: 400;
            background: transparent;
            border: 1px solid #fc2561;
            cursor: pointer;
            padding: 10px 25px;
            &:hover {
                background: #fc2561;
                color: #fff;
                border: none;
            }
        }
    }
}
.payment {
    display: flex;
    justify-content: center;
    .payment-title {
        p {
            color: #5e5e5e;
            font-family: Roboto;
            font-size: 16px;
            font-weight: 300;
        }
        .images {
            img {
                &:nth-child(1) {
                    margin-right: 30px;
                }
            }
        }
    }
}
.footer-logo {
    max-width: 1526px;
    margin: 0 auto;
    img {
        margin-left: 40px;
    }
    p {
        color: #5e5e5e;
        margin-left: 40px;
        font-family: Roboto;
        font-size: 12px;
        font-weight: 400;
        padding: 30px 0 30px 0;
        a {
            color: #fc2561;
            text-decoration: none;
        }
    }
}

#upbutton {
    background: url("../img/arrow.png") no-repeat center;
    height: 60px;
    width: 60px;
    bottom: 200px;
    right: 100px;
    cursor: pointer;
    display: none;
    position: fixed;
    z-index: 999;
    border-radius: 50%;
    border: 1px solid;
    &:hover {
        opacity: 0.4 !important;
    }
}
.mobile-reg {
    display: none;
}
.sections-mobile {
    display: none;
}