.container-brands {
    max-width: 1920px;
    margin: 0 auto;
    margin-top: 20px;
}
.brands-title {
    max-width: 1526px;
    height: 188px;
    background-color: #ffffff;
    margin: 0 auto;
}
.brands-item {
    display: flex;
    justify-content: space-evenly;
    border-top: 1px solid #dcdedd;
    margin: 0 40px;
    padding-top: 15px;
    div {
        margin-top: auto;
        margin-bottom: auto;
    }
}
.brands-text  {
    padding-top: 25px;
    p {
        color: #222222;
        font-family: "Roboto - Thin";
        font-size: 20px;
        font-weight: 400;
        margin: 0 0 0px 40px;
        width: 215px;
        border-bottom: 2px solid #282828 !important;
    }
}