@media (min-width: 320px) and (max-width: 575.98px) {
    .container-footer {
        border-top: 2px solid #dcdedd;
    }
    #upbutton {
        margin: 0 -90px -170px 0;
    }
    .footer-logo {
        img {
            position: absolute;
            margin: -370px 0 0 80px;
            width: 50%;
        }
        p {
            text-align: center;
            margin: 0 55px;
        }
    }
    .footer-title {
        display: block;
        .sections {
            display: none;
        }
        .info {
            display: none;
        }
        .sections-mobile {
            text-align: center;
            padding: 20px 0;
            border-top: 1px solid #dcdedd;
            margin-top: 90px;
            .dropright {
                .dropdown-menu {
                    top: 55px !important;
                    left: 44% !important;
                    border: 2px solid #fc2561;
                    padding-bottom: 0;
                    ul {
                        padding: 0;
                        li {
                            list-style: none;
                            text-align: center;
                            a {
                                color: #fc2561 !important;
                            }
                        }
                    }
                }
            }
            .dropright .dropdown-toggle::after {
                content: url('../img/arrow-mobile.png') !important;
                border-top: 0 solid transparent;
                border-right: 0;
                border-bottom: 0 solid transparent;
                border-left: 0 solid;
            }
        }
        .section-btn {
            height: 35px;
            background: transparent;
            border: none;
            font-size: 20px !important;
            text-transform: uppercase;
            padding: 0;
            color: #222;
            &:focus {
                box-shadow: none !important;
                outline: 0;
            }
        }
    }
    .payment {
        padding: 20px 0 20px 0;
        border-top: 1px solid #dcdedd;
        .payment-title {
            p {
                text-align: center;
                b {
                    display: none;
                }
            }
        }
    }
    .mobile-reg {
        display: block;
        border-bottom: 1px solid #dcdedd;
        border-top: 1px solid #dcdedd;
        padding: 15px 0 5px 0;
        .prepend {
            p {
                font-family: Verdana;
                font-size: 13px;
                font-weight: 400;
                text-align: center;
                span {
                    margin-left: 15px;
                    color: #fc2561;
                    font-family: Verdana;
                    font-size: 15px;
                    font-weight: 400;
                    border-bottom: 1px solid #fc2561;
                }
            }
        }
        a {
            text-decoration: underline;
            color: #fc2561;
            text-align: center;
        }
    }
    .sections-mobile {
        display: block;
    }
    .slick-prev, .slick-next {
        display: none !important;
    }
}
@media (min-width: 576px) and (max-width: 768px) {
    .footer-title {
        flex-wrap: wrap;
        .info {
            display: flex;
            flex-wrap: wrap;
            justify-content: end;
            .foot-title2 {
                position: absolute;
                display: flex;
                flex-direction: row;
                top: 175px;
                left: -455px;
                a {
                    p {
                        margin: 0;
                    }
                }
                p {
                    line-height: 1.2;
                    position: absolute;
                    margin: 22px 0 0 0;
                }
                .button {
                    margin: 10px 0 0 200px;
                }
            }
        }
    }
    .payment {
        justify-content: flex-end;
    }
    .footer-logo {
        margin-top: 50px;
        p {
            padding-bottom: 0;
        }
    }
    #upbutton {
        bottom: 50px;
        right: 50px;
    }
}
