@media (min-width: 320px) and (max-width: 575.98px) {
    .container-header {
        display: none;
    }
    .container-title {
        .title {
            display: block;
            div:nth-child(1) {
                display: none;      
            }
            div:nth-child(2) {
                img {
                    width: 50%;
                    margin-left: -25px;
                }
            }
        }
    }
    .navbar-toggler {
        position: absolute;
        top: -62px;
        left: 15px;
    }
    .buttons {
        .buttons-left {
            display: block !important;
            p {
                display: none;
            }
            form {
                margin: -38px -17px 0 0;
                input {
                    &:focus {
                        width: 190px;
                        background: #fff;
                    }
                }
            }
        }
        .buttons-center {
            display: none;
        }
        .buttons-right {
            button {
                width: 30px;
                height: 30px;
                position: absolute;
                top: -33px;
                right: 0;
                img {
                    width: 18px;
                    
                }
            }
            p {
                display: none;
            }
        }
    }
    .navbar {
        padding: 0 !important;
    }
    .container-navbar {
        border-top: 2px solid #656969;
        border-bottom: 0px solid #dcdedd;
        padding: 0;
    }
    .navbar-nav {
        .nav-item {
            margin-left: auto !important;
            margin-right: auto !important;
        }
    }
}

@media (min-width: 576px) and (max-width: 768px) {
    .container-header {
        display: none;
    }
    .container-title {
        .title {
            display: block;
            div:nth-child(1) {
                display: none;      
            }
            div:nth-child(2) {
                img {
                    width: 30%;
                    margin-left: -335px;
                }
            }
        }
    }
    .tel {
        display: flex;
        flex-direction: column;
        position: absolute;
        margin: -56px 0 0 315px;
        a {
            font-size: 15px;
            margin-right: 10px;
        }
        .btn-hed {
            height: 30px;
            color: #fc2561 !important;
        }
    }
    .navbar-toggler {
        position: absolute;
        top: -62px;
        left: 30px;
    }
    .buttons {
        margin-top: -18px;
        .buttons-left {
            display: block !important;
            p {
                display: none;
            }
            form {
                margin: -32px -17px 0 0;
                input {
                    &:focus {
                        width: 190px;
                        background: #fff;
                    }
                }
            }
        }
        .buttons-center {
            display: block;
            margin-top: -33px;
            p {
                display: none;
            }
            button {
                background: url('../img/heart.png');
            }
        }
        .buttons-right {
            button {
                width: 43px;
                height: 43px;
                position: absolute;
                top: -33px;
                right: 0;
                img {
                    width: 24px;
                }
            }
            p {
                display: none;
            }
        }
    }
    .navbar {
        padding: 0 !important;
    }
    .container-navbar {
        border-top: 2px solid #656969;
        border-bottom: 0px solid #dcdedd;
        padding: 0;
    }
    .navbar-nav {
        padding: 0 30px !important;
        flex-direction: row;
        .nav-item {
            margin-left: auto !important;
            margin-right: auto !important;
        }
    }
}

@media (min-width: 1024px) and (max-width: 1280px) {
    .title {
        button {
            margin: 6px 0 0 0 !important;
        }
    }
    .navbar-nav .nav-item {
        font-size: 15px;
    }
}