.container-card {
    max-width: 1920px;
    margin-top: 20px;
}
.card-block {
    max-width: 1526px;
    height: 575px;
    background-color: #ffffff;  
    margin: 0 auto;
}
.card-img-top {
    width: auto;
    height: 270px;
    margin: auto;
    &:nth-child(4){
        width: 166px;
        height: 286px;
    }
}
.card {
    border: none;
    margin: 0 10px;
}
.card-body {
    height: 100px;
}
.card-footer {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    background-color: #fff;
}
.btn {
    width: 150px !important;
    display: flex;
    justify-content: space-around;
    color: #ffffff !important;
    font-family: "BebasNeueBook";
    font-size: 18px;
    font-weight: 400 !important;
    letter-spacing: 1.35px;
    height: 55px;
    b {
        font-weight: inherit;
        margin: auto;
    }
    img {
        width: 24px;
        height: 19px;
        margin: auto;
    }
}
.text-muted {
    margin-top: auto;
    color: #000000 !important;
    font-family: "Arial - Bold MT";
    font-size: 20px;
    font-weight: 700;
}
.muted {
    display: flex;
    flex-direction: column;
}
.crossed {
    text-decoration:line-through;
}
.color {
    color: #f44336 !important;
}
.text-card {
    width: 130px;
    background: #000 !important;
    color: #fff;
    text-align: center;
    margin: 30px 0 0 -10px;
}
.popular {
    p {
        color: #222222;
        font-family: "Roboto - Thin";
        font-size: 20px;
        font-weight: 400;
        margin: 0 0 0px 40px;
        width: 160px;
        padding-top: 25px;
        border-bottom: 2px solid #282828 !important;
    }
}
.description {
    display: none;
}
.quantity {
    display: none;
}
.card-popular {
    display: none;
}