body {
    max-width: 1920px;
    margin: 0 auto;
    background-color: #f9f9f9;
}
.container-header {
    max-width: 1920px;
    height: 41px;
    border-radius: 1px;
    border: 1px solid #656969;
    background-color: #000000;
    margin: 0 auto;
}
.header-item {
    max-width: 1526px;
    margin: 0 auto;
    display: flex;
}
.item-right {
    display: flex;
    justify-content: flex-end;
}
.item-right a {
    color: #a0a0a0;
    font-family: Verdana;
    font-size: 17px;
    font-weight: 400;
    margin-bottom: auto;
    margin-top: 6px;
    margin-left: 50px;
    text-decoration: none;
    &:hover {
        color: #fff;
    }
}
.input-group-prepend {
    color: #a0a0a0;
    font-family: Verdana;
    font-size: 13px;
    font-weight: 400;
}
.input-group-prepend p {
    margin-bottom: auto;
    margin-top: auto;
    font-size: 17px;
    margin-right: 15px;
}
#inputGroupSelect03 {
    flex: none;
    width: 140px;
    color: #fefefe;
    font-family: Verdana;
    font-size: 17px;
    font-weight: 400;
    background: #000;
    border: none;
    cursor: pointer;
}
.custom-select {
    background: url('../img/arrow-select.png') no-repeat right .75rem center !important;
    background-size: 15px !important;
}
.custom-select option {
    background-color: #000;
}
.container-title {
    max-width: 1927px;
    border-radius: 1px;
    margin: 0 auto;
}
.title {
    display: flex;
    max-width: 1526px;
    margin: 0 auto;
    padding-top: 25px;
    justify-content: space-around;
    div:nth-child(1){
        display: flex;
        justify-content: space-between;
    }
    div:nth-child(2){
        text-align: center;
    }
}
.title {
    a {
        font-size: 21px;
        margin-bottom: auto;
        margin-top: auto;
        text-decoration: none;
        color: #000;
    }
    p {
        margin-bottom: auto;
        margin-top: auto;
        text-align: end;
        &:nth-child(1):hover {
            opacity: 0.4 !important;
        }
    }
}
.title button {
    background-color: transparent;
    &:hover {
        color: #a0a0a0;
    }
}
.buttons {
    display: flex;
    justify-content: flex-end;
}
.buttons-left {
    text-align: center;
    display: flex;
    flex-direction: column;
    form {
        position: relative;
        width: 300px;
        margin: 0 auto;
        height: 42px;
        input {
            height: 42px;
            width: 0;
            padding: 0 44px 0 15px;
            border: 2px solid transparent;
            background: transparent;
            outline: none;
            transition: 0.4s cubic-bezier(0, 0.8, 0, 1);
            position: absolute;
            top: 0;
            right: 0;
            z-index: 2;
            cursor: pointer;
            &:focus {
                width: 300px;
                z-index: 1;
                border: 2px solid #F9F0DA;
            }
        }
        button {
            border: none;
            height: 42px;
            width: 42px;
            position: absolute;
            top: 0;
            right: 0;
            &:before {
                font-size: 16px;
                color: #F9F0DA;
            }
        }
    }
}
.buttons-center {
    text-align: center;
    margin-left: 40px;
    button {
        cursor: pointer;
        width: 43px;
        height: 43px;
        background-color: #fc2561;
        border-radius: 50%;
        border: none;
        margin-left: auto;
        &:hover {
            opacity: 0.4;
        }
    }
    p {
        margin: 0;
    }
}
.buttons-right {
    text-align: center;
    margin-left: 40px;
    button {
        cursor: pointer;
        width: 43px;
        height: 43px;
        background-color: #fc2561;
        border-radius: 50%;
        border: none;
        &:hover {
            opacity: 0.4;
        }
    }
    p {
        margin: 0;
    }
}

.container-navbar {
    max-width: 1927px;
    margin: 0 auto;
    border-radius: 1px;
    border-top: 1px solid #656969;
    border-bottom: 2px solid#dcdedd;
    margin-top: 20px;
}
.container-navbar .navbar {
    margin: 0 auto;
    max-width: 1526px;
}
#navbarNav {
    justify-content: space-around;
}
.navbar-nav {
    width: 100%;
    flex-wrap: wrap;
}
.navbar-nav .nav-item {
    margin-left: auto;
    margin-right: auto;
    font-family: "Bebas Neue Book";
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 1.35px;
    text-transform: uppercase;
    &:nth-child(1) {
        margin-left: 0;
    }
    &:nth-child(11) {
        margin-right: 0;
    }
}
.btn {
    color: #222222 !important;
    font-family: "Arial MT" !important;
    font-size: 14px !important;
    font-weight: 400 !important;
    margin-bottom: auto;
    margin-top: auto;
    &:hover {
        color: #fc2561 !important;
        border: 1px solid #fc2561;
    }
}

.form-group {
    display: flex;
    flex-direction: column;
    label {
        text-align: left;
        font-weight: bold;
        font-size: 20px;
    }
}
.button {
    color: #222222;
    font-family: "Arial MT";
    font-size: 14px;
    font-weight: 400;
    background: transparent;
    border: 1px solid #fc2561;
    cursor: pointer;
    padding: 10px 25px;
    &:hover {
        background: #fc2561;
        color: #fff !important;
        border: none;
    }
}

.btn-hed {
    width: 150px !important;
    display: flex;
    justify-content: space-around;
    letter-spacing: 1.35px;
    height: 55px;
    color: #222222 !important;
    font-family: "Arial MT" !important;
    font-size: 14px !important;
    font-weight: 400 !important;
    margin-bottom: auto;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    border-radius: .25rem;
    b {
        margin-bottom: auto;
        margin-top: auto;   
    }
}
.tel {
    display: none;
}